<template>
  <main>
    <div>
      <div class="venues" v-for="(venue, venueIndex) in venues">
        <div class="venue">
          {{venue.name}}
        </div>
        <div class="time">
          {{venue.time}}
        </div>
        <div class="performances" v-for="(performance, performanceIndex) in venue.performances">
          <div class="performer">
            {{performance.performer}}
          </div>
          <div class="country">
            {{performance.country}}
          </div>
          <div class="title">
            {{performance.title}}
            <br>{{performance.title2}}
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
  import articles from '../assets/articles'

  export default {
    data() {
      return {
        venues: [],
        articles
      }
    },
    mounted() {
      this.loadArticle(this.$route.params.date)
    },
    methods: {
      loadArticle(date) {
        const article = this.articles.find(anArticle => anArticle.date === date);

        if (article) {
          this.venues = article.venues;
        } else {
          this.venues = 'No article'
        }
      }
    },
    watch: {
      $route(to, from) {
        const date = to.params.date;
        this.loadArticle(date)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .venues {
    position: relative;
    margin: 10px;
    padding: -1px;
    padding-bottom: 10px;
    overflow: hidden;
    background: white;
    box-shadow: 5px 5px 5px black;
    border-radius: 10px;
  }

  .venue {
    background: #CC1F7A;
    padding: 10px;
    text-align: center;
    outline: 0;
    border: 0;
    margin: -1px;
    border-radius: 2px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: white;
  }

  .venue:hover {
    background: #CC1F7A;
    transition: all .4s ease-in-out;
  }

  .time {
    padding: 9px;
    border-bottom: 1px solid #CC1F7A;
    font-size: 0.7em;
    text-align: center;
  }

  .performer {
    font-size: 0.8em;
    text-transform: uppercase;
    color: darkslategrey;
    text-align: center;
    padding-top: 10px;
  }

  .country {
    font-size: 0.6em;
    color: #CC1F7A;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
  }

  .title {
    font-size: 2.5em;
    color: #47C7CD;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
    padding-top: 50px;
    padding-bottom: 50px;

  }
</style>



