import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Workshops from '@/components/Workshops'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/date/:date',
      component: HelloWorld
    },
    {
      path: '/workshops',
      component: Workshops
    }
  ],
  mode: 'history'
})



