<template>
  <div id="app">
    <div class="container">
      <div class="row">
        <site-header></site-header>
      </div>
      <div class="row content">
        <div class="col-sm-3">
          <b-navbar toggleable="md" type="light">

            <!-- Put the toggle here after the brand -->
            <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

            <b-collapse is-nav id="nav_collapse">

              <b-nav vertical>
                <b-nav-item>
                  <router-link to="/date/08-06-2019">08-06-2019</router-link>
                </b-nav-item>
                <b-nav-item>
                  <router-link to="/date/09-06-2019">09-06-2019</router-link>
                </b-nav-item>
                <b-nav-item>
                  <router-link to="/date/10-06-2019">10-06-2019</router-link>
                </b-nav-item>
                <b-nav-item>
                  <router-link to="/date/11-06-2019">11-06-2019</router-link>
                </b-nav-item>
                <b-nav-item class="bg-workshop-menu">
                  <router-link  to="/workshops">WORKSHOPS</router-link>
                </b-nav-item>
              </b-nav>


            </b-collapse>
          </b-navbar>
        </div>
        <div class="col">
          <router-view/>
        </div>
      </div>
      <div class="row">
        <site-footer></site-footer>
      </div>
    </div>
  </div>
</template>

<script>
  import SiteHeader from './components/Header'
  import SiteFooter from './components/Footer'
  import SiteWorkshops from './components/Workshops'

  export default {
    name: 'App',
    components: {
      SiteHeader,
      SiteFooter,
      SiteWorkshops
    }
  }
</script>

<style lang="scss">
  @import '~bootstrap/scss/bootstrap';

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }

  body {
    background-color: black;
  }

  .content {
    margin-top: 10%;
  }

  .navbar-toggler {
    background-color: #47C7CD;
  }

  li {
    background: #CC1F7A;
    border-radius: 6px;
    margin: 10px;
    box-shadow: 5px 5px 5px black;
  }

  .bg-workshop-menu {
    background-color: #47C7CD;
  }

  a:hover, a:focus, a:active {
    color: black;
    text-decoration: none;
  }

  a {
    font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
    text-decoration: none;
    transition: color 0.1s, background-color 0.1s;
  }

  a {
    position: relative;
    display: inline-block;
    padding: 16px 0;
    margin: 0 12px;
    letter-spacing: 1px;
    font-size: 12px;
    line-height: 20px;
    font-weight: 900;
    text-align: center;
    text-transform: uppercase;
    transition: color 0.1s, background-color 0.1s, padding 0.2s ease-in;
    color: white;
  }

  a::before {
    content: '';
    display: block;
    position: absolute;
    bottom: 3px;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: white;
    transform-origin: right top;
    transform: scale(0, 1);
    transition: color 0.1s, transform 0.2s ease-out;
  }

  a:active::before {
    background-color: white;
  }

  a:hover::before, a:focus::before {
    transform-origin: left top;
    transform: scale(1, 1);
  }
</style>



