<template>
  <main>
    <ul class="inline">
      <li class="workshop" v-for="(workshop, workshopIndex) in workshops">
        <div class="date">
          {{workshop.date}}
        </div>
        <div class="venue">
          {{workshop.venue}}
        </div>
        <div class="time">
          {{workshop.time}}
        </div>
        <div class="teacher">
          {{workshop.teacher}}
        </div>
        <div class="title">
          {{workshop.workshop}}
        </div>
      </li>
    </ul>
  </main>
</template>

<script>
  import workshops from '../assets/workshops'

  export default {
    data() {
      return {
        workshops
      }
    },
  }
</script>

<style scoped>
  @media (min-width: 320px) and (max-width: 700px){
    .inline {
      padding-left: 0;
    }
  }

  .inline > li {
    display: inline-block;
  }
  .workshop {
    position: relative;
    width: 300px;
    height: 200px;
    margin: 10px;
    padding: -1px;
    padding-bottom: 10px;
    overflow: hidden;
    background: white;
    box-shadow: 5px 5px 5px black;
    border-radius: 10px;
  }

  .venue {
    background: #CC1F7A;
    padding: 10px;
    text-align: center;
    outline: 0;
    border: 0;
    margin: -1px;
    border-radius: 2px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: white;
  }

  .venue:hover {
    background: #CC1F7A;
    transition: all .4s ease-in-out;
  }

  .date {
    background: #47C7CD;
    padding: 10px;
    text-align: center;
    outline: 0;
    border: 0;
    margin: -1px;
    border-radius: 2px;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: white;
  }

  .date:hover {
    background: #47C7CD;
    transition: all .4s ease-in-out;
  }

  .time {
    padding: 9px;
    border-bottom: 1px solid #CC1F7A;
    font-size: 0.7em;
    text-align: center;
  }

  .teacher {
    font-size: 0.8em;
    text-transform: uppercase;
    color: darkslategrey;
    text-align: center;
    padding-top: 10px;
  }

  .title {
    font-size: 1em;
    color: #47C7CD;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;

  }
</style>



